@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(42deg, rgba(216, 255, 253, 1) 0%, rgba(175, 211, 255, 1) 100%);
}

#root {
  height: 100vh;
  display: grid;
  place-items: center;
}

@keyframes loader {
  from {
    transform: translate(-50%, -50%) scale(0.6);
  }
  to {
    transform: translate(-50%, -50%) scale(1);
  }
}

@keyframes show {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes exite {
  from {
    opacity: 1;
    filter: blur(0px);
  }

  to {
    opacity: 0;
    filter: blur(6px);
  }
}

.exit {
  animation: exite 500ms both;
}

.show {
  animation: show 500ms both;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;

  .img__container {
    position: relative;
    border-radius: 20px;
    box-shadow: 0 0 5px black;
    overflow: hidden;
    width: 300px;
    height: 200px;
    background-color: white;

    img {
      object-fit: cover;
      width: 300px;
      height: 200px;
      display: block;
    }

    .icon__container {
      position: absolute;
      inset: 0;

      svg {
        animation: loader 200ms infinite both linear;
        position: absolute;
        font-size: 6rem;
        color: blue;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        z-index: 10;
      }
    }
  }

  button {
    border-radius: 50%;
    padding: 10px;
    width: 80px;
    aspect-ratio: 1/1;
    background: url("https://api.iconify.design/carbon:skip-forward-outline-solid.svg") no-repeat
      center center / contain;
    background-size: 80px;
    border: none;
    transition: 500ms;

    &:hover {
      background-color: rgb(0, 77, 179);
      cursor: pointer;
    }

    &:active {
      transform: scale(0.8);
    }
  }
}

@keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}

$clr1: rgb(0, 116, 211);

.anim__container {
  padding: 50px;
  height: 300px;
  aspect-ratio: 1/1;
  position: relative;
  display: grid;
  place-items: center;
  overflow: hidden;

  &::before,
  &::after {
    position: absolute;
    content: "";
  }

  &::before {
    width: 100%;
    height: 100%;
    left: -50%;
    top: -50%;
    background: linear-gradient(42deg, rgba(178, 255, 252, 1) 0%, rgba(1, 109, 237, 1) 100%);
    animation: rotate 1s infinite linear;
    transform-origin: bottom right;
    z-index: -10;
  }

  &::after {
    width: 95%;
    aspect-ratio: 1/1;
    z-index: -5;
    background-color: rgb(109, 191, 247);
  }
}
